const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior(to, from, savePosition){
    if (/(list|order|service|interest)$/i.test(to.name)){
      console.log(to.name, "move -=>", store.state.scrollTop);
      setTimeout(() => {
        document.getElementById("container").scrollTo(0, store.state.scrollTop);
      }, 500);
    }
    else {
      document.getElementById("container").scrollTo(0, 0);
    }
    return {x:0, y:0};
  }
});

router.beforeEach((to, from) => {
  if (to.meta.requiresAuth && !store.getters.authorized) {
    if (typeof WeixinJSBridge == "object"){
      let url = "http://api.fuchijihua.com/wechat/?host=" + window.location.host + "&page=" + to.fullPath;
      return window.location.replace(url, true);
    }
    return {
      path: '/login',
      query: {redirect: to.fullPath},
    }
  }
  store.commit("toggleTabbar", "Home|Category|Cart|Bill|MyIndex|TeamIndex|VIP".indexOf(to.name) > -1);
  return true;
});

router.afterEach((to, from) => {
  const toDepth = to.path.split('/').length;
  const fromDepth = from.path.split('/').length;
  to.meta.transition = "van-fade";
  toDepth > fromDepth && (to.meta.transition = "van-slide-left");
  toDepth < fromDepth && (to.meta.transition = "van-slide-right");
});

export default router
