import { createStore } from 'vuex'
import createPersistedState from "vuex-persistedstate";

export default createStore({
  plugins: [createPersistedState()],
  state: {
    showPopup: false,
    showTabbar: false,
    showGrid: false,
    scrollTop: 0,
    config: null,
    profile: null,
    channel: [
      {name: "福利商城", type: "mall"},
      {name: "京东自营", type: "jdmall"},
      //{name: "网易严选", type: "wymall"},
    ],
  },
  getters: {
    authorized(state){
      if (state.profile == null || typeof state.profile != "object")
        return false;
      if (typeof state.profile.id == "undefined" || typeof state.profile.authorization != "string" || state.profile.id <= 0)
        return false;
      return true;
    },
    uid(state, getters){
      if (getters.authorized)
        return state.profile.id;
      return 0;
    },
    mobile(state, getters){
      if (getters.authorized)
        return state.profile.mobile.replace(/^(\d+)(\d{4})(\d{4})$/, "$1****$3");
      return "";
    },
    nickname(state, getters){
      if (getters.authorized)
        return (state.profile.nickname != null && state.profile.nickname.length > 1) ? state.profile.nickname : "福友" + state.profile.mobile.substr(-4);
      return "";
    },
    account(state, getters){
      if (getters.authorized && state.profile.account)
        return state.profile.account;
      return "TSjYANND5eSA83uZRWvpJXx8sYDMFgJppS";
    },
    cash(state, getters){
      if (getters.authorized && state.profile.cash)
        return parseFloat(state.profile.cash.replace(/(\.\d\d)(\d+)$/, "$1"));
      return 0;
    },
    point(state, getters){
      if (getters.authorized && state.profile.point)
        return parseFloat(state.profile.point.replace(/(\.\d\d)(\d+)$/, "$1"));
      return 0;
    },
    level(state, getters){
      if (getters.authorized)
        return state.profile.level || 0;
      return 0;
    },
    address(state, getters) {
      if (!getters.authorized || typeof state.profile.cartAddress != "object" || state.profile.cartAddress == null || state.profile.cartAddress.jdcode == "")
        return "广东省深圳市南山区南头街道";
      return state.profile.cartAddress.province + state.profile.cartAddress.city + state.profile.cartAddress.county;
    },
    addressCode(state, getters) {
      if (!getters.authorized || typeof state.profile.cartAddress != "object" || state.profile.cartAddress == null || state.profile.cartAddress.jdcode == "")
        return "19_1607_3155_62113";
      return state.profile.cartAddress.jdcode;
    },

  },
  mutations: {
    togglePopup(state, status){
      state.showPopup = typeof status == "boolean" ? status : !state.showPopup;
    },
    toggleTabbar(state, status){
      state.showTabbar = typeof status == "boolean" ? status : !state.showTabbar;
    },
    toggleGrid(state, status){
      state.showGrid = typeof status == "boolean" ? status : !state.showGrid;
    },
    setScrollTop(state, top) {
      state.scrollTop = top;
    },
    init(state, config){
      //console.log("Set config:", config);
      state.config = config;
    },
    auth(state, profile){
      //console.log("Set profile:", profile);
      state.profile = (profile == null || typeof profile != "object" || typeof profile["id"] == "undefined") ? null : {...state.profile, ...profile};
    },
    cartNumberSet(state, num){
      state.profile.cartNumber = parseInt(num);
    },
    cartNumberAdd(state, num){
      state.profile.cartNumber = parseInt(state.profile.cartNumber) + parseInt(num);
    },
  },
  actions: {
  },
  modules: {
  }
})
