import axios from 'axios'
import qs from 'qs'
import store from './store.js'
import router from './router.js'
import {Toast, Notify, Dialog} from 'vant'

axios.defaults.baseURL = process.env.VUE_APP_API_BASE;

axios.interceptors.request.use((config) => {
  config.method != "get" && Toast({type: "loading", message: "请稍候 ...", duration: 10000, forbidClick: true});
  
  let header = (typeof config.data == "object" && config.data instanceof FormData) ? {"Content-Type": "multipart/form-data"} : {"Content-Type": "application/x-www-form-urlencoded"};
  config.headers = Object.assign(config.headers, header, {"x-requested-with": "xmlhttprequest"});
  if (store.state.profile && store.state.profile.authorization)
    config.headers['token'] = store.state.profile.authorization;
  
  config.paramsSerializer = function (params) {
    return qs.stringify(params, {arrayFormat: "repeat"});
  };
  config.transformRequest = [function (data, headers) {
    if (data instanceof FormData)
      return data;
    return qs.stringify(data,  {arrayFormat: "brackets"});
  }];

  config.url += (config.url.indexOf("?") > -1 ? "&" : "?") + "rnd=" + Math.random();

  return config;
});

axios.interceptors.response.use((response) => {

  Toast.clear();

  if (response.status !== 200 || typeof response.data.rtn != "number"){
    Notify({type: "danger", message: "服务异常，请重试！", duration: 10000});
    return Promise.reject(response.data);
  }

  if (response.data.rtn == 999){
    Toast("授权已失效");
    store.commit("auth", null);
    router.push({
      path: '/login',
      query: {redirect: router.currentRoute.value.fullPath},
    });
    return Promise.reject(response.data);
  }

  if (response.data.rtn == 998){
    axios.get("/user/codesend");
    Dialog.confirm({
      title: response.data.msg,
      message: "<div style='margin:20px 5px;line-height:14px;'><input id='vcode' value='' style='width:90%;font-size:24px;text-align:center;' placeholder='请输入验证码'/><p>验证码已发送至当前账号绑定的手机</p></div>",
      theme: "round-button",
      allowHtml: true,
      beforeClose: (action) => new Promise((resolve) => {
        if (action == "cancel") return resolve(true);
        axios.get("/user/codecheck?code=" + document.querySelector("#vcode").value).then(() => resolve(true)).catch(() => resolve(false));
      }),
    });
    return Promise.reject(response.data);
  }

  if (response.data.rtn == 996){
    store.commit("auth", response.data.data);
    router.push({
      path: '/bind',
      query: {subtitle: response.data.msg, redirect: router.currentRoute.value.query.redirect || router.currentRoute.value.fullPath},
    });
    return Promise.reject(response.data);
  }

  if (response.data.rtn == 9){
    router.push({
      path: '/error',
      query: {msg: response.data.msg},
    });
    return Promise.reject(response.data);
  }

  if (response.data.rtn != 0){
    Notify({type: "danger", message: response.data.msg, duration: 15000, onClick: Notify.clear});
    return Promise.reject(response.data);
  }

  response.data.msg.length > 2 && Toast(response.data.msg);
  return Promise.resolve(response.data.data);

}, error => {

  Notify({type: "danger", message: "网络请求错误，请重试！", duration: 5000});
  console.error("网络错误：", error);

});

export default axios
